import React, { useState, createContext, useContext } from 'react';
import { cn } from '../../lib/utils';

// 标签页上下文
interface TabsContextType {
  activeTab: string;
  setActiveTab: (id: string) => void;
  orientation: 'horizontal' | 'vertical';
}

const TabsContext = createContext<TabsContextType | undefined>(undefined);

// 使用标签页上下文
const useTabsContext = () => {
  const context = useContext(TabsContext);
  if (!context) {
    throw new Error('Tabs components must be used within a Tabs container');
  }
  return context;
};

// 标签页容器属性
interface TabsProps {
  defaultValue?: string;
  value?: string;
  onValueChange?: (value: string) => void;
  orientation?: 'horizontal' | 'vertical';
  className?: string;
  children: React.ReactNode;
}

// 标签页容器
export const Tabs: React.FC<TabsProps> = ({
  defaultValue,
  value,
  onValueChange,
  orientation = 'horizontal',
  className,
  children
}) => {
  const [internalValue, setInternalValue] = useState(defaultValue || '');
  const activeTab = value !== undefined ? value : internalValue;

  const setActiveTab = (id: string) => {
    if (value === undefined) {
      setInternalValue(id);
    }
    onValueChange?.(id);
  };

  return (
    <TabsContext.Provider value={{ activeTab, setActiveTab, orientation }}>
      <div
        className={cn(
          'w-full',
          orientation === 'vertical' ? 'flex' : 'block',
          className
        )}
      >
        {children}
      </div>
    </TabsContext.Provider>
  );
};

// 标签页列表属性
interface TabsListProps extends React.HTMLAttributes<HTMLDivElement> {}

// 标签页列表
export const TabsList: React.FC<TabsListProps> = ({ className, ...props }) => {
  const { orientation } = useTabsContext();

  return (
    <div
      className={cn(
        'inline-flex h-10 items-center justify-center rounded-md bg-gray-100 p-1 text-gray-500',
        orientation === 'vertical' && 'flex-col h-auto w-40',
        className
      )}
      {...props}
    />
  );
};

// 标签页触发器属性
interface TabsTriggerProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  value: string;
  disabled?: boolean;
}

// 标签页触发器
export const TabsTrigger: React.FC<TabsTriggerProps> = ({
  value,
  disabled = false,
  className,
  children,
  ...props
}) => {
  const { activeTab, setActiveTab, orientation } = useTabsContext();
  const isActive = activeTab === value;

  return (
    <button
      className={cn(
        'inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium ring-offset-white transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-gray-400 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50',
        isActive
          ? 'bg-white text-gray-950 shadow-sm'
          : 'text-gray-600 hover:text-gray-950',
        orientation === 'vertical' && 'w-full justify-start',
        className
      )}
      disabled={disabled}
      onClick={() => setActiveTab(value)}
      {...props}
    >
      {children}
    </button>
  );
};

// 标签页内容属性
interface TabsContentProps extends React.HTMLAttributes<HTMLDivElement> {
  value: string;
}

// 标签页内容
export const TabsContent: React.FC<TabsContentProps> = ({
  value,
  className,
  children,
  ...props
}) => {
  const { activeTab, orientation } = useTabsContext();
  const isActive = activeTab === value;

  if (!isActive) return null;

  return (
    <div
      className={cn(
        'mt-2 ring-offset-white focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-gray-400 focus-visible:ring-offset-2',
        orientation === 'vertical' && 'ml-4 mt-0',
        className
      )}
      {...props}
    >
      {children}
    </div>
  );
};

// 标签页组属性
interface TabsGroupProps {
  label?: string;
  children: React.ReactNode;
  className?: string;
}

// 标签页组（用于垂直布局中的分组）
export const TabsGroup: React.FC<TabsGroupProps> = ({
  label,
  children,
  className
}) => {
  return (
    <div className={cn('mb-4', className)}>
      {label && (
        <h3 className="mb-2 text-sm font-medium text-gray-700">{label}</h3>
      )}
      {children}
    </div>
  );
};

// 组合组件
export const TabsRoot = Tabs;
export const TabsTab = TabsTrigger;
export const TabsPanel = TabsContent;

// 默认导出
export default Tabs;
